import CommonTitle from 'components/CommonTitle'
import custHeader from  'components/custHeader'
import FormItem from 'components/FormItem'
import ButtonWrap from "components/ButtonWrap"
    export default {
        components: {
            CommonTitle,
            custHeader,
            FormItem,
            ButtonWrap
        },
        created(){
            let _this = this;
            axiosUtil.getCreditInfo().then(data => {
                console.log(data);
                if(data.code == 0){
                    let obj = data.data || {};
                    _this.needRealName = obj.needRealName;
                    let {
                        borrowingDuration,
                        borrowPurpose,
                        identityCode,
                        realCapital,
                        totalInterestRatio,
                        userName
                    } = obj;
                    _this.formVals = {
                    borrowingDuration,
                    borrowPurpose,
                    identityCode,
                    realCapital,
                    totalInterestRatio,
                    userName
                    };
                }else{
                    //toast
                }
                
            })
        },
        methods:{
            mySubmit: function(){
                axiosUtil.saveCreditInfo(this.formVals).then(data => {
                    if(data.code == 0){
                        console.log(data.msg);

                    }else{
                        //toast
                        console.log(data.msg)
                    }
                
                })
            },
            updateFormVals: function(val){
                this.formVals[val.name] = val.value;
            }
        },
        data(){
            return{
                title: '洋葱金融系统',
                needRealName: true,
                formVals:{
                    borrowingDuration:"",
                    borrowPurpose:"",
                    identityCode:"",
                    realCapital:"",
                    totalInterestRatio:"",
                    userName:""
                }
            }
        }
    }



